


*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(230,230,244,1) 0%, rgba(196,228,246,1) 0%, rgba(229,230,244,1) 0%, rgba(187,227,246,1) 40%, rgba(0,212,255,1) 100%);
}
.navbar{
  
}
.navbar-brand{
  font-size: 1.3em !important;
  
}
.navbar a{
  font-size: 1em;
  text-transform: capitalize;
  font-weight: 500;
}
.menu_active{
 font-weight: bold;
 border-bottom: 1px solid black;
}
.navbar a:hover{
 color: #3498db;
}

#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
 
}
#header h1{
 margin: 0 0 10px 0;
 font-size: 42px;
 font-weight: 450;
 color: black;

}

#header .brand-name{
color:#3498db;

}
#header h2{
  color: #484848;
  font-size: 20px;
}

#header .btn-get-started{
 font-weight: 500;
 font-size: 16px;
 letter-spacing: 1px;
 display: inline-block;
 padding: 10px 30px;
 transition: 0.5s;
 border-radius: 50px;
 color:#3498db;
 border : 2px solid #3498db;
 text-decoration: none;
}
#header .btn-get-started:hover{
  background-color: #3498db;
  color: white;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
footer{
  padding: 0;
  margin: 0;
}

@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}


@media (max-width:991px){
  #header{
    height: 80vh;
    text-align: center;
  }
  #header .header-img{
    text-align: center;
  }

  #header .header-img-img{
    width: 60%;
  }
}

@media (max-width:991px){
  #header{
    margin-top: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img-img{
    width: 70%;
  }
}

